<template>
  <div :class="`product-form__header ${blockClass}`">
    <h1
      :id="`section-title-${liquid.sectionId}`"
      class="product-form__title h4"
      v-text="productTitle"
    />

    <product-price-message
      v-if="hasPricingMessage"
      :lowest-price="variant?.lowestPrice || false"
      :recommended-price="compareAtPrice > variant.price.amount ? $formatMoney(compareAtPrice) : false"
    />

    <div class="product-form__price-wrapper">
      <product-prices
        :available="variant?.available"
        block-class="product"
        :compare-at-price="compareAtPrice"
        :price="variant?.price?.amount || liquid.price"
      />
    </div>

    <div v-html="reviews" />

    <p
      v-if="ecoTaxMessage"
      class="product-form__ecotax-message body-2"
      v-text="ecoTaxMessage"
    />

    <div
      v-if="blockClass === 'mobile-hide'"
      class="product-form__short-desc"
    >
      <ul>
        <li
          v-for="(description, index) in shortDesc"
          :key="index"
          v-text="description"
        />
      </ul>
    </div>
  </div>
</template>

<script>
import ProductPriceMessage from '~global/product-price-message/product-price-message'

export default {
  name: 'ProductFormHeader',
  components: {
    ProductPriceMessage,
  },
  props: {
    blockClass: {
      type: String,
      default: '',
    },
    liquid: {
      type: Object,
      default: () => ({
        hasOnlyDefaultVariant: false,
      }),
    },
    product: {
      type: [Boolean, Object],
      default: false,
      required: true,
    },
    variant: {
      type: [Boolean, Object],
      default: false,
      required: true,
    },
    missingVariant: {
      type: Boolean,
      default: false,
    },
    reviews: {
      type: String,
      default: () => (''),
    },
  },

  computed: {

    /**
     * Compute product title.
     */
    productTitle() {
      if (this.liquid.translations.title) {
        return this.liquid.translations.title
      } else if (this.product.title) {
        return this.product.title
      }

      return this.liquid.title
    },

    /**
     * Compute short description.
     */
    shortDesc() {
      return this.liquid.translations.shortDesc || this.variant.shortDesc
    },

    /**
     * Get eco tax message.
     */
    ecoTaxMessage() {
      const ecoTaxMessage = this.$string('product.form.eco_tax')

      if (!this.variant?.ecoTax || this.variant?.ecoTax === '') {
        return
      }

      return ecoTaxMessage.replace( '{{amount}}', this.variant.ecoTax)
    },

    /**
     * Compute current compare at price.
     */
    compareAtPrice() {
      return this.variant?.compareAtPrice?.amount || false
    },

    hasPricingMessage() {
      const enabled = this.$variable('settings.pricingDirective.enable')

      if (!enabled || this.missingVariant) {
        return false
      }

      return this.compareAtPrice > this.variant.price.amount || this.variant.lowestPrice
    },
  },

  mounted() {
    if (window?.yotpoWidgetsContainer &&
    typeof window.yotpoWidgetsContainer.initWidgets === 'function') {
      this.reviewsLoadAttempts = 0
      window.yotpoWidgetsContainer?.initWidgets()
      return
    }
  },
}
</script>
